.App {
  text-align: center;
}

.App-logo {
  height: 10vmin;
  pointer-events: none;
  display:inline-block;
  width: 10vmin;
  padding: 10px;
  padding-bottom: 0px;
}

.App-header {
  background-color: rgba(255, 255, 255, 0.1);
  display: flex;
  align-items: left;
  justify-content: top;
  font-size: calc(10px + 2vmin);
  color: rgb(0, 0, 0);
  border-style:solid;
  border-bottom-width: 1px;
  border-width: 0px 0px 1px 0px;
  border-color:black;
  position:sticky;
    top:0;
  backdrop-filter:blur(10px);
    box-shadow: 2px 7px 7px 1px rgba(0,0,0,0.3);
  z-index:1;
}

.header-button{
  text-decoration: none;
  color: black;
  margin: 10px;
  margin-top:auto;
  margin-bottom:auto;
}

.log{
  align-items:right;
  font-size: 15px;
  margin-top:auto;
  margin-bottom:auto;
}

.logsign{
  margin-left:auto;
  text-align: center;
  margin-top:auto;
  margin-bottom:auto;
}

.BannerImage{
  display:inline-block;
}

.Article1{
  margin: auto;
  margin-left: auto + 10%;
  margin-right:5%;
  display:inline-block;
  width:18%;
  height:30%;
  border-radius:10%;
  border-color:rgb(141, 141, 141);
  border: 1px solid rgb(141, 141, 141);
  backdrop-filter:blur(10px);
  box-shadow: 2px 7px 7px 1px rgba(0,0,0,0.3);
  z-index:0;
}
.ArticleImage{
  margin-left:auto;
  margin-right:auto;
  margin-top:4%;
  margin-bottom:auto;
  width:18%;
  height:15%;
  border-radius:50%;
  border: 1px solid rgb(141,141,141);
  backdrop-filter:blur(10px);
    box-shadow: 2px 2px 2px 2px rgba(0, 0, 0, 0.3);
  z-index:0;
}